* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li{
  list-style: none;
}

.country-full{
  height: 200px;
  margin-bottom: 70px;
}

.country-full-first-line{
  display: flex;
  align-items: center;
}

.country-short{
  display: flex;
  align-content: center;
  align-items: center;
  height: 30px;
  margin-bottom: 20px;
}

.flag{
  width: 40px;
  margin-right: 10px;
}

.text-bold{
  font-weight: 700;
}
